<template>
  <div class="content">
    <div class="head">
      <i class="el-icon-arrow-left title" @click="back"></i>
      <span>QAQ</span>
    </div>
    <el-carousel trigger="click" height="150px" arrow="aways">
      <el-carousel-item>
        <img src="../../assets/img/fanfan.jpg" alt="">
      </el-carousel-item>
      <el-carousel-item>
        <img src="../../assets/img/pic1.jpg" alt="">
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  methods: {
    back () {
      this.$router.push('/index')
    }
  }
}
</script>

<style lang="scss" scoped>
div {
  box-sizing: border-box;
}
.content {
  font-family: "Hiragino Sans GB";
  .head{
    height: 50px;
    background-color: #fff;
    padding: 0 2%;
    line-height: 50px;
    position: relative;
    text-align: center;
    font-size: 14px;
    i {
      width: 20px;
      height: 20px;
    }
    .title {
      position: absolute;
      left: 0;
      line-height: 50px;
      margin-left: 1%;
    }
  }
  .el-carousel__item img {
    width: 100%;
  }
  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
}
</style>



